@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.home, .reports, .products {
  display: -webkit-flex;
  display: flex;
  height: 90vh;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 3rem;
}
 .type {
   max-width: 400px;
   margin: 0 auto;
   text-align: center;  
   color:green;
 }
 .type label {
float: left;
 }

 .type span {
display: block;
overflow: hidden;
padding: 0px 4px 0px 6px;
 }

 .type button {
   float: right;
   position: relative;
   
 }
.navbar {
    background-color: rgb(121, 138, 153);
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items: center;
            align-items: center;
}

.menu-bars {
   margin-left: 2rem;
   font-size: 2rem;
   background: none; 
}

.nav-menu {
background-color:#28848c;
width: 250px;
height: 100vh;
display: -webkit-flex;
display: flex;
-webkit-justify-content: center;
        justify-content: center;
position: fixed;
top: 0;
left: -100%;
transition: 850ms;
 }

 .nav-menu.active {
     left: 0;
     transition: 350ms;
     }

.nav-text {
display: -webkit-flex;
display: flex;
-webkit-justify-content: start;
        justify-content: start;
-webkit-align-items: center;
        align-items: center;
padding: 8px 0px 8px 16px;
list-style: none;
height: 60px;
}

.nav-text a {
text-decoration: none;
color: aliceblue;
font-size: 18px;
width: 95%;
height: 100%;
display: -webkit-flex;
display: flex;
-webkit-align-items: center;
        align-items: center;
padding: 0 16px;
border-radius: 4px;
}

.nav-text a:hover {
background-color: #08a4ac;
}

.nav-menu-items {
width: 100%;
}

.navbar-toggle {
     background-color: rgb(113, 126, 138);
     width: 100%;
     height: 80px;
     display: -webkit-flex;
     display: flex;
     -webkit-justify-content: start;
             justify-content: start;
     -webkit-align-items: center;
             align-items: center;
}

span {
    margin-left: 16px;
}
